body{background:#2e3138}


.main-container{display:grid;overflow:hidden;padding:.5vw;grid-gap:.5vw;gap:.5vw;grid-template-columns:minmax(345px, 2fr) minmax(230px, 1fr) minmax(230px, 1fr);grid-template-rows:auto}@media only screen and (max-width: 820px){.main-container{grid-template-columns:minmax(345px, 2fr) 0 minmax(230px, 1fr)}}@media only screen and (max-width: 590px){.main-container{grid-template-columns:minmax(345px, 2fr) 0 0}}

.users-container{height:90vh;overflow:hidden;display:flex;flex-direction:column;background:#18181b}.users--header{display:flex;justify-content:space-between}.users--main{display:grid;overflow-y:scroll;height:100%;align-content:flex-start;grid-template-columns:repeat(auto-fit, minmax(230px, 1fr));scrollbar-color:#717174 rgba(24,24,27,0.772549)}.users--main::-webkit-scrollbar{width:1vw}.users--main::-webkit-scrollbar-track{background:rgba(24,24,27,0.772549)}.users--main::-webkit-scrollbar-thumb{background-color:#717174;border-radius:1px}.users--main::-webkit-scrollbar-button{height:3px;background:rgba(24,24,27,0.772549)}.droppable--inactive{background:#18181b;background:repeating-linear-gradient(45deg, #6e0000, #6e0000 17px, #18181b 17px, #18181b 50px)}

.search-users-input{min-width:225px;height:1.5rem;margin:5px;padding:18px;padding-left:15px;border-radius:4px;font-size:0.9rem;font-family:Arial, Helvetica, sans-serif;border:white;outline:none;color:#cccccc;background:#23252b}.search-users-input:focus{color:white}

.room-switches-container{width:auto;display:flex;flex-flow:row wrap;align-items:center;justify-content:flex-end;grid-row-gap:1px;row-gap:1px;grid-column-gap:1px;-webkit-column-gap:1px;column-gap:1px;margin:5px 10px 2px 10px}.room-switch{width:27px;height:25px;border-radius:5px;background-color:#3f3f3f}.room-switch:hover{background-color:#3f3f3fa5;cursor:pointer}

.user{height:100px;display:flex;align-items:center;justify-content:space-between;color:#fff;margin:5px;border-radius:3px;overflow:hidden;transition:.3s ease-in;box-sizing:border-box;background:#3a3a3d;cursor:-webkit-grab;cursor:grab}.active-user:hover{transition:.3s ease-in;background:#111160}.user img{width:70px;height:70px;display:inline-block;overflow:hidden;color:#0000;margin:0 10px;border-radius:100%;background:#23252b}

.activeUsers-container{max-height:90vh;overflow-y:scroll;background:#444953;scrollbar-color:#717174 #444953}.activeUsers-container::-webkit-scrollbar{width:1vw}.activeUsers-container::-webkit-scrollbar-track{background:#444953}.activeUsers-container::-webkit-scrollbar-thumb{background-color:#717174;border-radius:1px}.activeUsers-container::-webkit-scrollbar-button{height:3px;background:#444953}.droppable--active{background:rgba(68,73,83,0.686275)}

.actions-container{height:90vh;display:grid;align-items:flex-end;grid-template-rows:auto auto;grid-gap:0.5vw;gap:0.5vw}

.server-actions-container{background:#444953}

.page-actions-container{display:grid;grid-gap:1vw;padding:5%;grid-template-rows:repeat(2, minmax(100px, 10vw));grid-template-columns:repeat(2, minmax(100px, 10vw));align-content:center;justify-content:center;background:#444953}

.action{display:flex;align-items:center;justify-content:center;padding:10px;border-radius:20%;background:#666b75}.action span{color:white;font-family:"Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;font-size:1rem}.action:hover{cursor:pointer}.action:hover img{-webkit-animation:rotate 2.1s ease infinite;animation:rotate 2.1s ease infinite}@-webkit-keyframes rotate{to{transform:rotate(360deg)}}@keyframes rotate{to{transform:rotate(360deg)}}.action img{width:100%;height:100%;border-radius:inherit;box-sizing:border-box;border:0px solid black;transition:border 0.02s ease-in;border-radius:40%}.action img:active{transition:border 0.02s ease-in;border:.25rem solid black;border-radius:40%}


